@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Roboto&display=swap);
body, html {
  margin: 0;
  padding: 0;
  background-color: #f1f1f1;
}

::selection {
	background: #67f07c;
	color: whitesmoke;
}

::-moz-selection {
	background: #67f07c;
	color: whitesmoke;
}
#game-controlls {
	display: flex;
	margin: 10%;
	margin-bottom: 4%;
	justify-content: space-around;
	align-items: center;
}

#game-controlls .options-input {
	min-width: 100px;
	letter-spacing: .15em;
	margin: 15% auto;
	text-align: center;
}

@media screen and (max-width: 650px) {
	#game-controlls {
		flex-direction: column;
	}

	#game-controlls .options-input {
		/*width: 100px;*/
		margin: 7.5% auto;
	}

	#game-controlls #options {
		margin-top: 5%;
	}
}
header {
	height: 125px;
	background-image: url(/static/media/h-bg.6c07f1de.jpg);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: top center;
	display: flex;
	justify-content: center;
	align-items: center;
	font-family: 'Montserrat', sans-serif;
	position: relative;
}

header h1 {
	margin: 0;
	color: whitesmoke;
	font-size: 3.5em;
	letter-spacing: .25em;
	text-align: center;
}

#score {
	position: absolute;
	color: whitesmoke;
	bottom: 0;
	left: 1.5%;
}

@media screen and (max-width: 600px) {
	header h1 {
		font-size: 2.75em;
		letter-spacing: .15em;
	}

	#score {
		font-size: .75em;
	}

	#flag {
		margin-top: 5%;
	}
}

@media screen and (max-width: 850px) {
	header {
		position: static;
		position: initial;
	}

	#score {
		top: 125px;
		color: black;
	}
}
#flag {
	width: 450px;
	height: 275px;
	margin-right: 5%;
}

@media screen and (max-width: 600px) {
	#flag {
		width: 80%;
		height: 30%;
		max-width: 450px;
		max-height: 175px;
	}
}
#options {
	font-family: 'Roboto', serif;
	font-style: italic;
}

.options-input label, .options-input input {
	cursor: pointer;
}
#result-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 2% 10%;
	font-family: 'Montserrat', sans-serif;
	text-align: center;
}

#result-container #result {
	font-size: 1.25em;
	margin-bottom: 0;
}

#continue {
	cursor: pointer;
	text-decoration: underline;
	letter-spacing: .15em;
}
